import MessageComponent from './Message.vue'

const Message = {
  install(Vue) {
    const Constructor = Vue.extend(MessageComponent) //Message
    let instance = new Constructor({
      el: document.createElement('div') //Messagediv
    })
    document.body.appendChild(instance.$el) //body
    //vue
    Vue.prototype.$message = (msg,type, confirmSure = () => {}) => {
      instance.message = msg //
      type ? instance.type = type : instance.type = 'success'
      instance.show = true //message
      setTimeout(() => {
        instance.show = false
      }, 3000);
      instance.confirmSure = confirmSure //
    }
  }
}
export default Message

